import * as React from 'react';
import { Controller, RenderMarkProps } from '@ali/4ever-cangjie';
import { QuickInsertMark } from './types';
import Mark from './components/quickInsertMark';

export default function createRenderMark() {
  return function renderMark(props: RenderMarkProps, controller: Controller, next) {
    const { mark, children } = props;
    const { type } = mark as QuickInsertMark;
    if (type !== 'quick-insert') return next();
    return <Mark mark={mark}>{children()}</Mark>;
  };
}
